@import "../../assets/css/parts/variables";
@import "../../assets/css/parts/mixins";

html.lb-disable-scrolling ,
body.lb-disable-scrolling {
    overflow: hidden;
  }
  
  .lightboxOverlay {
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    z-index: 9999;
    // filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
    // opacity: 0.8;
    // display: none;
  }
  
  .lightbox {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    left: 0;
    width: 100vw;
    z-index: 10000;
    text-align: center;
    line-height: 0;
    font-weight: normal;
    outline: none;
    top: 0 !important;
    height: 100vh !important;
  }
  
  .lightbox .lb-image {
    display: block;
    height: auto !important;
    max-width: 100vw;
    max-height: none;
    margin: 0 auto;
    position: relative;
  }
  
  .lightbox a img {
    border: none;
  }
  
  .lb-outerContainer {
    position: relative;
    *zoom: 1;
    width: 250px;
    height: 100vh !important;
    margin: 0 auto;
    min-width: 100vw;
    max-height: 100vh;
    overflow-y: scroll;
  }
  
  .lb-outerContainer:after {
    content: "";
    display: table;
    clear: both;
  }
  
  .lb-loader {
    left: 0;
    position: absolute;
    top: 0;
    height: 100vh;
    width: 100vw;
    background-color: #000;
    z-index: 100000;
    pointer-events: none;
    opacity: 0;
    transition: all $speed $easing;
  }
  
  .lb-cancel {
    // display: block;
    // width: 32px;
    // height: 32px;
    // margin: 0 auto;
    //background: url(../images/loading.gif) no-repeat;
  }
  
  .lb-nav {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100vw;
    z-index: 10;
    &:hover {
        cursor: url(../../../assets/images/cursors/cursor-close.png) 40 40, auto;
    }
  }

  .lb-container {
    display: flex;
    flex-direction: column;
    justify-content: center;


      position: relative;
      min-height: 100%;
      height: auto;
      width: 100vw;
    //   overflow-y: scroll;
      &:after {
        background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0) 1%,rgba(0,0,0,0.4) 100%); 
        content: '';
        display: block;
        position: fixed;
        top: 0;
        height: 100%;
        width: 100%;
        z-index: 9;
      }
  }
  
  .lb-container > .nav {
    left: 0;
  }
  
  .lb-nav a {
    outline: none;
    background-image: url('data:image/gif;base64,R0lGODlhAQABAPAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==');
  }
  
  .lb-prev, .lb-next {
    height: 100%;
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 25%;
  }
  
  .lb-nav a.lb-prev {
    left: 0;
    // float: left;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
    opacity: 0;
    transition: opacity 0.6s;
  }
  
  .lb-nav a.lb-prev:hover {
    cursor: url(../../../assets/images/cursors/cursor-prev.png) 40 40, auto;
    opacity: 1;
  }
  
  .lb-nav a.lb-next {
    right: 0;
    opacity: 0;
    transition: opacity 0.6s;
  }
  
  .lb-nav a.lb-next:hover {
    cursor: url(../../../assets/images/cursors/cursor-next.png) 40 40, auto;
    opacity: 1;
  }
  
  .lb-dataContainer {
    margin: 0 auto;
    padding: 0 15px;
    position: fixed;
    top: calc(100vh - 30px);
    *zoom: 1;
    max-width: calc(100% - 30px);
    z-index: 100;
  }
  
  .lb-dataContainer:after {
    content: "";
    display: table;
    clear: both;
  }
  
  .lb-data {
    padding: 0 4px;
    color: #ccc;
  }
  
  .lb-data .lb-details {
    width: 85%;
    float: left;
    text-align: left;
    line-height: 1.1em;
  }
  
  .lb-caption p {
      margin-bottom: 0;
    span {
        font-size: 10px;
        font-weight: 900;
        letter-spacing: .6px;
        text-transform: uppercase;
    }
}
  
  .lb-data .lb-caption a {
    color: #4ae;
  }
  
