  @include bp($t){
    .lb-dataContainer {
        top: auto;
        bottom: 20px;
    }
    .lb-caption {
        p {
            line-height: 0;
            &:first-child {
                margin-bottom: 5px;
            }
        }
        span {
            &.hit-title {
                font-family: $caligor;
                font-size: 34px;
                line-height: 41px;
            }
        }
    }
  }
  